body {
    background-color: #F6F6F6;
    &:before {
        content: "";
        height: 300px;
        top: 0;
        left: 0;
        width: 100%;
        position: fixed;
        background-image: linear-gradient(90deg, $tertiary 0%, #1940b0 100%);
    }
}

.page-heading {
	width: 100%;
    position: relative;
    background-image: linear-gradient(90deg, $tertiary 0%, #1940b0 100%);
	border-bottom: 1px solid rgba(0,0,0,.1);

	&:after {
		content: "";
		display: table;
		clear: both;
	}
	&.bg-transparent {
		background-color: transparent;
		background-image: none;

		.page-heading__container {
			.title {
				color: inherit;
			}
		}
	}

	.page-heading__container {
		.title {
			color: #ffffff;
		}
		&.float-right {
			padding-right: 10px;
		}
	}
}

.btn-grad {
    color: #fff;
    border: none;
    background-image: linear-gradient(to right, $primary 0%, $secondary 100%);
    transition: all .5s;
    &:hover,
    &:focus,
    &:active {
        color: #fff;
        border: none;
        background-image: linear-gradient(to right, $secondary 0%, $primary 100%);
    }
}

.btn-primary {
    background-color: $primary !important;
    border-color: $primary !important;
    transition: all .5s;
    &:hover,
    &:focus,
    &:active {
        background-color: $secondary !important;
        border-color: $secondary !important;
    }
}

.btn-secondary {
    background-color: $secondary !important;
    border-color: $secondary !important;
    transition: all .5s;
    &:hover,
    &:focus,
    &:active {
        background-color: $primary !important;
        border-color: $primary !important;
    }
}

.btn-block + .btn-block {
    margin-top: 0;
}

.navigation--quick {
    ul {
        li {
            a {
                padding-left: 40px;
                &:after {
                    color: inherit;
                    font-family: 'linearicons' !important;
                    content: "\e8da";
                    width: 0;
                    height: 0;
                    top: 9px;
                    left: 12px;
                    font-size: 1.4rem;
                }
                &.profile {
                    &:after {
                        content: "\e6d1";
                    }
                }
                &.preferences {
                    &:after {
                        content: "\e673";
                    }
                }
                &.security {
                    &:after {
                        content: "\e66b";
                    }
                }
                &.account {
                    &:after {
                        content: "\e759";
                    }
                }
            }
        }
    }
}

.form-control {
    padding: 19px 9px;
}

.mt--10 { margin-top: -10em !important; }
.mt--9 { margin-top: -9em !important; }

.text-primary { color: $primary !important }
.text-secondary { color: $secondary !important }
.text-tertiary { color: $tertiary !important }

.btn {
    &.disabled,
    &:disabled {
        cursor: not-allowed !important;
    }
}